/* LiveOrderTracker.css */

/* Add styles for the container */
div {
  border: 1px solid #ddd;
  padding: 20px;
  margin: 20px 0;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h2 {
  color: #e44d26;
}

p {
  margin: 10px 0;
}

/* Style for different order statuses */
p.Status-Processing {
  color: #e44d26;
}

p.Status-Shipped {
  color: #1abc9c;
}
