/* Feedback.css */

.Feedback {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

.Feedback h2 {
  color: #e44d26;
}

.Feedback textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  resize: vertical;
}

.Feedback div {
  margin-bottom: 10px;
}

.Feedback label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.Feedback select {
  padding: 8px;
}

.Feedback button {
  background-color: #e44d26;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.Feedback button:hover {
  background-color: #333;
}
